import React, { useEffect, useState } from "react";
import { PayPalScriptProvider, PayPalButtons } from "@paypal/react-paypal-js";
import CurrencyService from "../services/CurrencyService";

const CLIENT_ID = "ВАШ_CLIENT_ID"; // Замените на ваш реальный client id

function BuyTokens() {
  const [conversion, setConversion] = useState(null);
  const [currency, setCurrency] = useState("USD");
  const [tokens, setTokens] = useState(1);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    async function fetchConversion() {
      setIsLoading(true);
      try {
        const data = await CurrencyService.getConversionTable();
        setConversion(data);
        // Выбрать первую валюту по умолчанию
        const first = Object.keys(data).find(k => k !== "$id");
        setCurrency(first || "USD");
      } catch (e) {
        setError("Ошибка загрузки курсов валют");
      } finally {
        setIsLoading(false);
      }
    }
    fetchConversion();
  }, []);

  if (isLoading) return <div>Загрузка...</div>;
  if (error) return <div>{error}</div>;
  if (!conversion) return <div>Нет данных о валютах</div>;

  const handleCurrencyChange = e => setCurrency(e.target.value);
  const handleTokensChange = e => setTokens(Number(e.target.value));

  const rate = conversion[currency] || 1;
  const amount = (tokens * rate).toFixed(2);

  const handleApprove = (data, actions) => {
    return actions.order.capture().then(details => {
      // Отправить только orderID (токен оплаты) на сервер
      fetch("/Payments/paypal/complete", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data.orderID)
      });
      alert("Оплата прошла успешно!");
    });
  };

  return (
    <PayPalScriptProvider options={{ "client-id": CLIENT_ID, currency }}>
      <div style={{ maxWidth: 400, margin: "0 auto" }}>
        <h3>Покупка токенов</h3>
        <div>
          <label>Валюта:
            <select value={currency} onChange={handleCurrencyChange}>
              {Object.keys(conversion).filter(k => k !== "$id").map(cur => (
                <option key={cur} value={cur}>{cur}</option>
              ))}
            </select>
          </label>
        </div>
        <div>
          <label>Количество токенов:
            <input type="number" min={1} value={tokens} onChange={handleTokensChange} />
          </label>
        </div>
        <div>Сумма к оплате: <b>{amount} {currency}</b></div>
        <div style={{ marginTop: 20 }}>
          <PayPalButtons
            style={{ layout: "vertical" }}
            createOrder={(data, actions) => {
              return actions.order.create({
                purchase_units: [{
                  amount: { value: amount, currency_code: currency }
                }]
              });
            }}
            onApprove={handleApprove}
          />
        </div>
      </div>
    </PayPalScriptProvider>
  );
}

export default BuyTokens;
